<template>
  <div id='canteen' class='subPage'>
    <header>
      <div id='back' @click='back'>
        <img src='static/assets/back.png' alt='back'>
      </div>
      <div class='canteen-name'>
        <p>文山湖食堂</p>
        <div id="filter-box" @click="filter">
          <img src="static/assets/filter.png" alt="filter">
        </div>
      </div>
    </header>
    <div id='foods'>
      <div v-for='(item, index) in renderMenu' class='food-item' v-bind:index="index">
        <div class='item-content' @click='learnFood'>
          <a>
            <div class='fd-pic'></div>
          </a>
          <a>
            <div class='fd-intro'>
              <p>{{item.name}}</p>
              <p>月售357份</p>
              <p>好评率96%</p>
              <p>￥{{item.price}}}</p>
            </div>
          </a>
        </div>
        <div class='cartBox'>
          <img src='static/assets/buy.png' alt='buy'>
        </div>
      </div>
    </div>
    <div id='filter' class='hide-filter' @touchstart='touch' @touchmove='touch' @touchend='touch'>
    </div>
    <transition name='grey'>
      <div v-show='filtered' class='grey' @click.stop='quit'></div>
    </transition>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'canteen',
    data () {
      return {
        tag: false,
        filtered: false,
        y: 0,
        posX: 0,
        posY: 0,
        listenTouch: false
      }
    },
    methods: {
      back: function () {
        this.$router.push({path: '/center'})
      },
      wrapHeader: function () {
        var self = this
        $('#canteen').scroll(function () {
          var y = $(this).scrollTop()
          if (y >= 310) {
            self.tag = self.tag ? self.tag : !self.tag
            if (self.tag) {
              $('.canteen-name').addClass('fixed-name')
              $('.canteen-name p').css('-webkit-transform', 'translateX(' + 100 + 'px)')
              $('.canteen-name p').css('transform', 'translateX(' + 100 + 'px)')
              $('#back').addClass('fixed-back')
            }
          } else if (self.tag) {
            $('#back').removeClass('fixed-back')
            $('.canteen-name').removeClass('fixed-name')
            $('.canteen-name p').css('-webkit-transform', 'translateX(' + 0 + 'px)')
            $('.canteen-name p').css('transform', 'translateX(' + 0 + 'px)')
            self.tag = false
          }
        })
      },
      learnFood: function () {
        this.$root.previous = 1
        this.$router.push('/food')
      },
      filter: function () {
        this.filtered = true
        this.listenTouch = true
        $('#filter').addClass('animate-filter')
        $('#filter').addClass('show-filter')
        $('#filter').removeClass('hide-filter')
        setTimeout(function () {
          $('#filter').removeClass('animate-filter')
        }, 200)
      },
      quit: function () {
        this.filtered = false
        this.listenTouch = false
        $('#filter').removeClass('show-filter')
        $('#filter').removeAttr('style')
        $('#filter').addClass('animate-filter')
        $('#filter').addClass('hide-filter')
        setTimeout(function () {
          $('#filter').removeClass('animate-filter')
        }, 200)
      },
      test: function () {
        console.log(1)
      },
      touch: function (ev) {
        if (this.listenTouch) {
          switch (ev.type) {
            case 'touchstart':
              this.posX = ev.targetTouches[0].pageX
              this.posY = ev.targetTouches[0].pageY
              console.log('start')
              break
            case 'touchmove':
              var disx = ev.targetTouches[0].pageX - this.posX
              // var disy = ev.targetTouches[0].pageY - this.posY
              // var temp1 = disx < 0 ? -disx : disx
              // var temp2 = disy < 0 ? -disy : disy
              // if (disx > 0) {
              //   console.log('panRight')
              // } else {
              //   console.log('panLeft')
              // }
              break
            case 'touchend':
              // eslint-disable-next-line
              var disx = ev.changedTouches[0].pageX - this.posX
              break
          }
          if (disx > 0) {
            $('#filter').css('transform', 'translateX(' + disx + 'px)')
          }
          if (disx > 0.03 * this.$root.width) {
            this.filtered = false
            this.listenTouch = false
            $('#filter').removeAttr('style')
            $('#filter').removeClass('show-filter')
            $('#filter').addClass('animate-filter')
            $('#filter').addClass('hide-filter')
            setTimeout(function () {
              $('#filter').removeClass('animate-filter')
            }, 200)
          }
        }
      }
    },
    computed: {
      renderMenu: function () {
        return this.$store.state.menu
      }
    },
    mounted () {
      let self = this
      this.wrapHeader()
      $.ajax({
        async: false,
        url: '/dish/',
        type: 'POST',
        data: {
          restaurant_id: self.$store.state.restaurantIndex,
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token
        },
        success: function (data) {
          self.$store.commit('setMenu', data)
          console.log(data)
        },
        error: function () {
          alert('获取菜单失败！')
        }
      })
    }
  }
</script>

<style scoped>
  #canteen{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: white;
    z-index: 4;
  }
  header{
    width: 100%;
    height: 450px;
    background: #1296db;
    overflow: hidden;
  }
  #back{
    width: 14%;
    height: 140px;
    position: fixed;
    top: 0;
    left: 4%;
    z-index: 2;
    overflow: hidden;
  }
  #back img{
    width: 60px;
    margin-left: 50%;
    margin-top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    padding: 0;
  }
  .fixed-back{
    z-index: 4!important;
  }
  .canteen-name{
    position: relative;
    top: 310px;
    padding: 0 calc(8% - 25px) 0 8%;
    width: calc(84% + 25px);
    height: 140px;
    z-index: 3;
    overflow: hidden;
    background: #1296db;
  }
  .fixed-name{
    position: fixed!important;
    top: 0;
    left: 0;
  }
  .canteen-name p{
    font-size: 66px;
    line-height: 140px;
    color: white;
    float: left;
    -webkit-transition: -webkit-transform .2s;
    -moz-transition: -moz-transform .2s;
    -o-transition: -o-transform .2s;
    transition: transform .2s;
  }
  #filter-box{
    float: right;
    width: 140px;
    height: 140px;
    float: right;
    padding: 0;
    background-color: #1296db;
  }
  #filter-box img{
    width: 60px;
    margin: calc((100% - 60px) / 2);
    padding: 0;
  }
  #foods{
    width: 100%;
    height: auto;
    z-index: 1;
    margin-bottom: 30px;
  }
  .food-item{
    width: 100%;
    height: 340px;
    overflow: hidden;
  }
  .item-content{
    width: 84%;
    height: calc(100% - 3px);
    margin: 0 8%;
    z-index: 1;
  }
  .food-item:not(:last-of-type) .item-content{
    border-bottom: 3px solid #f0f2f5;
  }
  .food-item a{
    color: black;
  }
  .fd-pic{
    width: 240px;
    height: 240px;
    border-radius: 16px;
    background: url('../../static/assets/food.jpeg') 50% 50%;
    background-size: cover;
    top: 50px;
  }
  .fd-intro{
    width: calc(96% - 240px);
    height: 100%;
    left: 4%;
    z-index: 1;
  }
  .fd-pic, .fd-intro{
    position: relative;
    float: left;
  }
  .fd-intro p:first-of-type{
    font-size: 48px;
    font-weight: bold;
    margin-top: 54px;
  }
  .fd-intro p:nth-of-type(2), .fd-intro p:nth-of-type(3){
    float: left;
    font-size: 34px;
    margin-right: 22px;
    margin-top: 16px;
  }
  .fd-intro p:nth-of-type(4){
    clear: left;
    font-size: 46px;
    color: #FF5339;
    margin-top: 140px;
  }
  .cartBox{
    float: right;
    width: 12%;
    height: 120px;
    position: relative;
    bottom: 150px;
    right: calc(8% - 20px);
    z-index: 2;
  }
  .cartBox img{
    width: 80px;
    float: right;
    margin: 22px 11.5% 0 0;
  }
  #filter{
    width: 80%;
    height: 100%;
    top: 0;
    right: 0;
    position: fixed;
    background: white;
    z-index: 5;
  }
  .animate-filter{
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -webkit-transition: -webkit-transform .2s;
    -moz-transition: -moz-transform .2s;
    -o-transition: -o-transform .2s;
    transition: transform .2s;
  }
  .hide-filter{
    -webkit-transform: translateX(100%);
    -moz-transform: translateX(100%);
    -o-transform: translateX(100%);
    transform: translateX(100%);
  }
  .show-filter{
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0);
  }
</style>



